.btn {
    border: none;
    background: $extra-color;
    cursor: pointer;
    padding: 0 16px;
    height: 35px;
    display: flex;
    justify-content: center;
    align-items: center;
    font: normal normal normal 17px/20px Prompt;
    &:active, &:focus {
        outline: none; }
    &__arrow {
        cursor: pointer;
        background: $main-color;
        width: 55px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        // border-top-left-radius: -50%
        // border-bottom-left-radius: -50%
        &:before {
            content: '';
            border-radius: 0 75px 75px 0 / 0 300px 300px 0;
            background: red;
            height: 100%;
            width: 10px;
            left: 0;
            position: absolute;
            background: $extra-color; }

        &-icon {
            width: 17.311px;
            height: 17.311px; } } }


